.body-campaign{
    margin:24px 48px;
    background: #f2f2f2;
    padding: 42px 32px;
    width:90.5%;
    padding-bottom: 50px;
}
.column-content-campaign{
    border-radius:4px;
    text-align: center;
    display: grid;
    background:#2e2d31;
    height: 370px;
    color:#f2f2f2;
    cursor:pointer;
}
.column-content-campaign img{
    border-radius:4px;
    height: 300px;
    width: 300px;
    object-fit: cover;
    flex: 1 1 250px;
    
}
.columns-campaign{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.title-campaign{
    color:#2e2d31;
    font-size:28px;
    position: relative;
}
.title-underline-campaign{
    content: '';
    position: absolute;
    border: 1px solid #2e2d31;
    height:2px;
    width:320px;
    background:#2e2d31;
    top:4.5rem;
    left:4px;
}
.content-campaign{
max-width: 540px;
font-size:20px;
}
.link-campaign a{
    position: absolute;
    right:3rem;
    margin-top: 14px;
    text-decoration: none;
    align-items: right;
    color:#2e2d31;
    font-size:18px;
    font-weight:600;
}
.link-campaign a:hover{
    color:teal;
    text-decoration: 3px solid teal underline;
}