.body-about{
    z-index:1;
    background:teal;
    color:#f2f2f2;
    position:relative;
    width:95%;
    margin:24px 48px;
    height:510px;
}
.about-title{
    background: #f2f2f2;
    height: 75px ;
}
.about-title h1{
   margin:0 64px;
   color: #2e2d31;
   font-weight: 800;
   font-size: 48px;
   text-transform: capitalize;
}
.container-about{
    display:flex;
}
.sect-1-about img{
    width:650px;
height:470px;
object-fit: cover;
}
.sect-2-about{
    padding: 0 48px;
    width: 100%;
    display: grid;
    height: 410px;
    background: #2e2d31;
    
}
.sect-2-about h3{
font-weight: 700px;
font-size:48px;
}
.sect-2-about p{
    font-weight:500;
    font-size:18px;
}
.sect-2-about a{
    text-decoration: none;
    color:#7f91f0;
    font-weight: 600;
  
}
.sect-2-about a:hover{
    color:teal;
    text-decoration: 2px solid teal underline;
}