.team-body{
    padding:8px 24px;
    background:#2e2d31;
    height:470px;
}
.team-title{
    margin: 6px 48px;
   
   
}
.image-cont{
    width:250px;
    height: 350px;
  
}
.image-cont img{
    width:100%;
    height:100%;
    object-fit: cover;
    box-shadow:1px 3px rgba(0,0,0,.3);
}
.image-cont div{
    color:#f2f2f2;
    height: 50px;
    font-weight:600;
    vertical-align: middle;
    font-size:18px;
    background:#2e2d31;
    margin:24px auto;
    text-align: center;
}
.team-cont{
    padding:24px 0;
    display:flex;
    flex-wrap: wrap;
    justify-content: space-around;
   
}