.body-form{
    background:#f2f2f2;
    width:95%;
    margin:24px 48px;
    height:850px;
    position:relative;
    display: grid;
}
.title-form{
    text-align: end;
    color: #2e2d31;
font-size:24px;
 line-height: 16px;
 width:100%;

}
.title-form h1{
    margin-right: 15%;
}
.content-form{
    display:flex;
    justify-content: space-evenly;
}
.sect-1-form{
    height:750px;
    width:50%;
}
.sect-1-form img{
    height: 100%;
    width:100%;
    object-fit: cover;
}
.content-info{
    font-size: 20px;
    margin:12px 0;
}
.form-contents{
    width:50%;
}
.form-contents form{
    display: grid;
    margin:0 9%;
}
.form-contents input{
    width:100%;
    padding:4px 6px;
    height:40px;
    margin:4px 0;
    max-width:350px;
    border-radius:4px;
    border:none;
    border:1px solid #acaaaf;
    font-size:16px;
   
}
.form-contents input:focus{
    outline-color: #7f91f0;
}
#Amount{
    width:150px;
}
.form-contents .amount{
    display: grid;
}
.form-contents .amount .text-span{
    margin:auto 14px;
    font-weight:700;
    font-size:18px;
}
.form-contents .amount .text-info{
    font-size:18px;
    font-weight:500;
    margin:8px 0;
}
.form-contents textarea{
    border-radius:4px;
    border:1px solid #acaaaf;
    padding:4px 12px;
    margin:4px 0;
    font-size:16px;
    width:350px;
    outline: none;
}
.form-submit[type="submit"]{
    width:100%;
    max-width: 150px;
    background:#7f91f0;
    border:none;
    color:#f2f2f2;
    border-radius: 4px;
    font-size:24px;
    padding: 6px 16px;
    box-shadow: 5px 4px teal;
    cursor:pointer;
    margin-top:24px;
}
.form-submit[type="submit"]:hover{
    background:transparent;
    border:1px solid #7f91f0;
    color:#121212;
    box-shadow:none;
}