.body-nav{
    background:#f2f2f2;
    display: flex;
    justify-content: space-between;
}
.nav-logo{
    text-transform: uppercase;
    text-align: left;
    margin:auto 48px;
    font-size:32px;
    cursor: pointer;
}
.nav-items{
    display: flex;
    list-style: none;
    justify-content: space-evenly;
    flex:0.5;
    margin:auto;
    margin-right: 6%;
}
.nav-item a{
    text-decoration: none;
    color:#121212;
    font-weight: 500;
}
.nav-item a:hover{
    color:teal;
}