.main-footer{
    padding:24px 0;
    display: grid;
    background:#2e2d31;
    color:#f2f2f2;
}
.sub-footer{
    display: flex;
    justify-content: space-around;
}
.mini-footer{
    font-size:18px;
    font-weight:600;
    text-align:end;
    margin:6px 48px;
}
.mini-footer a{
    text-decoration: none;
    color:#f2f2f2;
}
.mini-footer a:hover{
    color:#7f91f0;
}
.logo-footer{
    font-size:40px;
}
.news-info{
    font-size:18px;
}
.news-cont{
    display: grid;
}
.news-cont input{
  color:#000;
  border:1px solid #2e2d31;
  border-radius: 4px;
  background: #f2f2f2;
    height: 30px;
    padding:8px 12px;
    font-size:16px;
    margin:12px 0;
}
.news-cont input:focus{
   
    outline-color: #7f91f0;
}
.news-cont input[type="submit"]{
    width:100%;
    height: 40px;
    max-width: 150px;
    background:#7f91f0;
    border:none;
    color:#f2f2f2;
    border-radius: 0;
    font-weight:600;
    padding: 6px 16px;
    box-shadow: 5px 4px teal;
    cursor:pointer;
}
.news-cont input[type="submit"]:hover{
    background:transparent;
    border:1px solid #7f91f0;
    box-shadow:none;
}
.links-footer{
    
    text-align: center;
}
.link-footer{
    font-size:20px;
    font-weight:600;
    margin:auto;
    
}
.links-footer ul{
    list-style: none;
    display: grid;
}
.links-footer li{
    width:100%;
 
    text-align: start;
    padding:12px 0;
    
}.links-footer a{
    text-decoration: none;
    color:#f2f2f2;
    font-weight:500;
}