.body-header{
    background:rgb(12, 236, 210);
    display:flex;
    margin-left:48px;
    width:95%;
    height: 500px;
    margin-top: 24px;
    position: relative;
}
.sect-1{
    z-index: 3;
    background:#e8e5dd;
}
.header-content{
    width:500px;
    padding: 42px 32px;
    margin:auto 48px;
    display: grid;
}
.sect-1 h3{
    font-weight:800;
    font-size: 40px;
    width: 420px;
}
.sect-1 p{
    font-weight:500;
    width: 400px;
    margin: 24px 0;
}
.sect-1 button{
    background:#7f91f0;
    border:none;
    color:#f2f2f2;
    border-radius: 4px;
    font-size:24px;
    padding: 6px 16px;
    box-shadow: 5px 4px teal;
    cursor:pointer;
}
.sect-1 button:hover{
    background:transparent;
    border:1px solid #7f91f0;
    color:#121212;
    box-shadow:none;
}
.sect-2{
    z-index:2;
    margin-left:-24px;
    position:absolute;
    left:50%;
    height:580px;
}
.sect-2 img{
object-fit: cover;
width:95%;
height:100%;
}
.header-links{
margin:48px 0;
display:flex;
justify-content: space-between;
width: 80%;
}
.header-links a{
    text-decoration:none;
    color: #121212;
    font-weight:500;
}
.header-links a:hover{
    color:teal;
}